body,body *,html{margin:0;padding:0}body{font-size:14px}button{cursor:pointer}select{margin:5px;min-width:100px}.red{background:red}.green{background:green}.yellow{background:#ff0}.flex-center{align-items:center;display:flex}.flex-center.column{-moz-box-align:center;flex-direction:column;justify-content:center}.flex-center.row{flex-direction:row}#root{height:100%;position:absolute;width:100%}#root>div{display:flex;height:100%}#root:after{clear:both;content:"";display:block}::-webkit-scrollbar{width:8px}::-webkit-scrollbar-thumb{background:#ccc}::-webkit-scrollbar-track{background:#eee}#content{border-left:1px solid #ccc;display:flex;flex-direction:column;float:left;width:calc(100% - 426px)}footer{bottom:10px;left:6px;position:fixed}footer a{background:#bbb;border-radius:4px;color:#fff;padding:1px 3px;text-decoration:none}footer a:hover{color:#000}