#aside-nav{background:#f8f8f8;border:1px solid #ececec;float:left;padding:12px 12px 24px;text-align:center}#aside-nav img{margin-bottom:30px}#aside-nav ul{list-style:none}#aside-nav li button{align-items:center;background:#fff;border:1px solid #ddd;border-radius:50%;color:#2dbe60;display:inline-flex;height:46px;justify-content:center;margin:10px 0;text-decoration:none!important;transition:background .15s ease-out;width:46px}#aside-nav .active button,#aside-nav li button:hover{background:rgba(45,190,95,.9);color:#fff}#aside-nav .active button{font-weight:700}