#note-info{background:#fff;border:1px solid #a8a8a8;border-radius:3px;box-shadow:1px 2px 15px rgba(0,0,0,.2);box-sizing:border-box;display:none;font-size:1rem;max-width:420px;min-width:340px;padding:20px 10px 20px 40px;position:fixed;right:28px;top:60px}#note-info li{line-height:1.8}#note-info li span:first-child{display:inline-block;width:70px}#note-info li span:last-child{margin-left:10px}#note-info .arrow{background:url(contents/img/arrow.gif) no-repeat 0 -43px;display:inline-block;height:10px;position:absolute;right:73px;top:-10px;width:20px}#note-info.active{display:block}