#note-list{border-right:1px solid #ececec;box-sizing:border-box;float:left;width:350px}#head-title{color:#878787;display:block;font-size:2em;font-weight:400;padding:15px 20px}#note-list{position:relative}#search-wrap{display:none;position:absolute;right:0;top:0}#search-wrap button{background:none;border:none;height:30px;width:30px}#head-info{border-bottom:3px solid #ececec;color:#878787;margin:0;padding:10px 20px}#note-list ul{height:calc(100% - 108px);list-style:none;overflow:auto}#note-list ul>li{cursor:pointer;padding:0 20px;position:relative}#note-list ul>li.active div div{background:rgba(45,190,95,.9)!important}#note-list ul>li:hover div div{background:rgba(45,190,95,.2)}#note-list ul>li.active div *{color:#fff!important}#note-list .list-wrap{border-bottom:1px solid #ececec;height:95px;padding:15px 0}#note-list .list-wrap>div{box-sizing:border-box;left:0;padding:10px 20px;position:absolute;top:0;width:100%}#note-list .list-wrap>div strong{display:block;font-size:1.2em;margin-bottom:5px}#note-list .list-wrap>div span{color:#878787;display:block;font-size:.8em;margin-bottom:5px}#note-list .list-wrap>div article{color:#878787;font-size:.9em;height:56px;line-height:1.5;overflow:hidden}#note-list .list-wrap ol,#note-list .list-wrap ul{margin:5px 0 0 20px}